import React from 'react';

import classes from './Works.module.css';



const Works = (props) =>{
    return (
        <div className={classes.Works}>
            
            <a href={props.link} target='_blank'><div className={classes.container}>
                <h3>{props.title}</h3>
            </div></a>
            <img src={props.img}></img>
        </div>
    )
}

export default Works;